// FOOTER
.footer {
  background: #F3F4F5;
  &__wrapper {
    display: flex;
    padding-bottom: 30px;
    margin-left: -15px;
    margin-right: -15px;
    color: $fontSecondary;
    span {
      padding-top: 10px;
      margin-top: auto;
      display: block;
      font-size: 13px; } }
  &__left {
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 0 15px; }

  &__right {
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 0 15px; }

  &__phone {
    font-size: 30px;
    line-height: 40px;
    display: inline-block;
    transition: color .25s $ease;
    &:hover {
      color: $brandColor; } }

  &__socials {
    display: flex;
    font-size: 0;
    align-items: center;
    a {
      display: inline-block;
      margin-right: 20px; } } }

@include r($desktop) {
  .footer {
    &__wrapper {
      span {
        font-size: 12px; } } } }

@include r($tablet) {
  .footer {
    &__wrapper {
      display: block;
      margin: 0;
      padding-bottom: 15px;
      span {
        font-size: 10px; } }
    &__left {
      flex: 0 0 auto;
      max-width: 100%;
      padding: 15px 0; }
    &__right {
      flex: 0 0 auto;
      max-width: 100%;
      padding: 10px 0; }
    &__phone {
      font-size: 21px;
      line-height: 1.2; } } }
