.hero {
  position: relative;
  background: #0a1f35;
  overflow: hidden;
  &::after {
    display: inline-block;
    content: ' ';
    position: absolute -10px -20px;
    z-index: 1;
    background: url('../img/hero-bg.jpg') no-repeat center;
    background-size: cover;
    filter: blur(5px);
    transition: filter .3s linear;
    @include retina {
      background-image: url('../img/hero-bg@2x.jpg'); } }
  &.is-ready {
    &::after {
      filter: blur(0); } }
  &__wrapper {
    position: relative;
    z-index: 2;
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
    min-height: 100vh;
    color: white;
    padding: 45px 0 55px; }

  &__left, &__right {
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    max-width: 50%;
    min-width: 1px;
    min-height: 0; }
  &__right {
    padding-left: 0; }

  &__content {
    margin: auto 0;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column; }

  &__logo {
    display: inline-block;
    font-size: 0px;
    margin-bottom: auto;
    margin-left: 45px;
    .ico {
      font-size: 62px; } }

  &__app {
    margin-bottom: 5px;
    font-size: 0;
    margin-left: -26px;
    // margin-bottom: -40px
    margin-top: -5px;
    height: 100px; }
  &__heading {
    margin-top: 25px;
    margin-bottom: 10px;
    font-size: 36px;
    line-height: 1.37;
    font-weight: 700;
 }    // line-height: 1.33

  &__description {
    margin-top: 15px;
    font-size: 21px;
    line-height: 1.4;
    color: $fontTetriary; }

  &__apps {
    margin-top: 25px;
 }    // margin-bottom: auto

  &__cta {
    max-width: 285px;
    margin-top: 20px;
    margin-bottom: auto; }


  &__scroll {
    position: absolute;
    bottom: 30px;
    left: 50%;
    text-align: center;
    cursor: pointer;
    &:hover {
      // animation-play-state: paused
      .hero__scroll-wrap {
        animation-play-state: paused;
 } }        // transform: translateY(5px)
    &-wrap {
      font-size: 0;
      animation: scrollAnimation 1.8s ease;
      animation-iteration-count: infinite;
      animation-fill-mode: both;
      // transition: transform .25s $ease
      .ico {
        font-size: 15px; } } } }

@keyframes scrollAnimation {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(8px); }
  100% {
    transform: translateY(0); } }

@include r($desktop) {
  .hero {
    &__logo {
      .ico {
        font-size: 54px; } }
    &__heading {
      font-size: 32px; }
    &__description {
      font-size: 19px; } } }

@include r($tablet) {
  .hero {
    &::after {
      background-position: 50% bottom;
      background-image: url('../img/hero-mobile.jpg');
      @include retina {
        background-image: url('../img/hero-mobile@2x.jpg'); } }
    &__wrapper {
      flex-direction: column;
      margin: 0;
      padding-top: 80px;
      padding-bottom: 40px;
      min-height: 600px !important;
      height: 100vh; }
    &__left, &__right {
      flex: 0 0 auto;
      padding: 0;
      max-width: 100%; }
    &__logo {
      margin-left: 0; }
    &__app {
      display: none; }
    &__heading {
      font-size: 21px;
      margin-top: 5px;
      margin-bottom: 5px; }
    &__description {
      font-size: 13px;
      margin-top: 5px; }
    &__logo {
      position: absolute;
      top: 20px;
      left: 0;
      .ico {
        font-size: 40px; } }
    &__apps {
      margin-top: 5px; }
    &__cta {
      margin-bottom: 0;
      max-width: 246px; }
    &__scroll {
      display: none; } } }
