
/* BENEFITS */
.benefits-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-right: 2%;
  &__col {
    flex: 0 1 33.3%;
    max-width: 33.3%;
    padding: 15px;
    margin-top: 20px;
    &:nth-child(3n) {
      p {
        margin-right: -10%; } }
    &:nth-child(n+4) {
      .heading {
        margin-top: 10px; } }
    .ico {
      overflow: visible;
      &-account {
        font-size: 44px; }
      &-time {
        font-size: 42px; }
      &-ready-send {
        font-size: 45px; }
      &-filter {
        font-size: 44px; }
      &-card-list {
        font-size: 42px; }
      &-chat {
        font-size: 47px; } }

    .heading {
      margin-top: 0px;
      margin-bottom: 10px; }
    p {
      color: $fontSecondary;
      font-size: 15px;
      line-height: 1.4;
      margin: 0;
      & + p {
        margin-top: 1em; } } }
  &__icon {
    min-height: 50px; } }

@include r($tablet) {
  .container-offset {}

  .benefits-list {
    &__col {
      flex-basis: 50%;
      max-width: 50%;
      margin-top: 10px;
      &:nth-child(3n) {
        p {
          margin-right: 0%; } }
      .heading {
        margin-bottom: 5px; }
      p {
        font-size: 13px; }
      .ico {
        overflow: visible;
        &-account {
          font-size: 33px; }
        &-time {
          font-size: 32px; }
        &-ready-send {
          font-size: 32px; }
        &-filter {
          font-size: 30px; }
        &-card-list {
          font-size: 30px; }
        &-chat {
          font-size: 32px; } } }
    &__icon {
      min-height: 40px; } } }

@include r($mobile) {
  .benefits-list {
    margin-right: -15px;
    &__col {
      flex-basis: 100%;
      max-width: 100%;
      margin-top: 0;
      p {
        font-size: 13px; }

      .heading {
        font-size: 15px; } } } }

@include r(374) {
  .benefits-list {
    &__col {
      .heading {
        font-size: 13px; }
      p {
        font-size: 11px; } } } }
