.steps {
  padding: 110px 0 95px;
}

.steps__wrapper {
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
}

.steps__left {
  flex: 1 1 auto;
  max-width: 615px;
  min-width: 1px;
  min-height: 0;
  padding: 0 40px 0 15px;
}

.steps__left .heading {
  margin-top: 20px;
  margin-left: 60px;
}

.steps__right {
  flex: 0 0 280px;
  max-width: 280px;
  min-width: 1px;
  min-height: 0;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

.steps__video {
  position: relative;
  font-size: 0;
  pointer-events: none;
}

.steps__video::after {
  display: inline-block;
  content: '';
  position: absolute;
  z-index: 1;
  top: 35px;
  bottom: -40px;
  left: -15px;
  right: -15px;
  background: url("../img/iphone-x-shadow.png") no-repeat center;
  background-size: cover;
}

.steps__video-phone {
  position: relative;
  z-index: 3;
  width: 100%;
}

.steps__video-frame {
  position: absolute;
  z-index: 2;
  top: 2.3%;
  left: 5.7%;
  right: 6.3%;
  bottom: 2.5%;
  overflow: hidden;
  border-radius: 25px;
}

.steps__video-frame video {
  position: absolute 0;
  width: 100%;
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
}

.steps__video-frame video.is-active {
  opacity: 1;
}

.steps__video-frame {
  background: #172A3F;
}

.step-nav {
  margin-top: 30px;
}

.step-nav__el {
  position: relative;
  display: flex;
  margin-bottom: 42px;
}

.step-nav__el:last-child {
  margin-bottom: 0;
}

.step-nav__el:first-child {
  cursor: default;
}

.step-nav__el:first-child::after {
  display: none;
}

.step-nav__el::after {
  display: none !important;
  display: inline-block;
  content: '';
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  height: 80px;
  background: url("../img/stepNav-arrow.png") no-repeat right center;
  opacity: 0;
  transform: translate(15px, 0);
  transition: opacity 0.25s ease-in-out, transform 0.25s ease-in-out;
}

.step-nav__el.is-active {
  pointer-events: none;
}

.step-nav__el.is-active::after {
  opacity: 1;
  transform: translate(30px, 0);
}

.step-nav__el:hover:not(.is-active)::after {
  opacity: .25;
}

.step-nav__icon {
  flex: 0 0 60px;
  transform: translateX(-3px);
}

.step-nav__icon span {
  font-size: 21px;
  font-weight: 700;
  color: #B5BBC2;
}

.step-nav__content {
  position: relative;
  z-index: 2;
  flex: 0 1 auto;
  max-width: 100%;
}

.step-nav__name {
  display: inline-block;
  margin-top: 9px;
  font-size: 22px;
  line-height: 1.27;
}

@media only screen and (max-width: 992px) {
  .steps {
    padding: 45px 0 40px;
  }
  .steps__left {
    padding-right: 15px;
  }
  .step-nav__name {
    font-size: 20px;
  }
}

@media only screen and (max-width: 768px) {
  .steps {
    padding: 20px 0 40px;
  }
  .steps__wrapper {
    display: block;
  }
  .steps__video {
    display: none;
  }
  .steps__left .heading {
    margin-left: 0;
  }
  .step-nav {
    margin-top: 20px;
  }
  .step-nav__el {
    margin-bottom: 17px;
    display: block;
  }
  .step-nav__el::after {
    display: none;
  }
  .step-nav__icon {
    flex-basis: 40px;
    max-width: 40px;
    transform: translate(0, 0);
  }
  .step-nav__icon span {
    font-size: 17px;
  }
  .step-nav__name {
    font-size: 13px;
  }
}

