.cta-form {
  background: #f03226;
  color: white;
  border-radius: 16px;
  padding: 40px 30px 100px;
}

.cta-form__heading {
  text-align: center;
}

.cta-form__heading .heading {
  margin: 0;
}

.cta-form__description {
  font-size: 18px;
  margin-top: 22px;
  text-align: center;
}

.cta-form__form {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
}

.cta-form__form .button {
  margin-top: 20px;
}

.cta-form__form .button:not([disabled]) {
  background-color: rgba(5, 206, 130, 0.7);
}

.cta-form__form .button:not([disabled]):hover {
  background-color: rgba(5, 206, 130, 0.9);
}

.cta-form__input {
  -webkit-apperance: none;
  background: transparent;
  box-shadow: none;
  vertical-align: middle;
  border: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 30px;
  font-weight: 300;
  color: white;
  margin: 0;
  border-radius: 4px;
  padding: 4px 15px;
  text-align: center;
  transition: 0.25s ease-in-out;
}

.cta-form__input::placeholder {
  color: white;
}

.cta-form__input:focus {
  box-shadow: 0 0 2px 1px #4d90fe;
  border-color: #aa2e2e;
}

.cta-form__checkbox {
  display: none;
  position: relative;
  margin-top: 20px;
}

.cta-form__checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.cta-form__checkbox input:checked + label::after {
  transform: scale(1);
}

.cta-form__checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.cta-form__checkbox label::before {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  content: ' ';
  size: 15px;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.cta-form__checkbox label::after {
  position: absolute;
  top: -2px;
  left: 4px;
  transform: scale(0);
  display: inline-block;
  content: ' ';
  size: 15px;
  background-image: svg-load("../icons/checkmark.svg", fill= "#fff");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: transform .2s ease;
}

.cta-form__checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-weight: 300;
  font-size: 14px;
  cursor: pointer;
}

.cta-form__checkbox label span a {
  display: inline;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: 0.25s ease-in-out;
}

.cta-form__checkbox label span a:hover {
  color: rgba(255, 255, 255, 0.7);
}

@media only screen and (max-width: 992px) {
  .cta-form__heading .heading {
    text-align: center;
    margin-left: 0;
  }
  .cta-form__description {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
  }
  .cta-form__checkbox {
    padding-left: 15px;
  }
  .cta-form__checkbox label span {
    padding-left: 25px;
  }
}

@media only screen and (max-width: 768px) {
  .cta-form {
    padding-bottom: 60px;
  }
  .cta-form__description {
    font-size: 13px;
  }
  .cta-form__input {
    font-size: 17px;
    padding: 11px 10px;
  }
}
