// INDEX


.heading {
  font-weight: 700;
  line-height: 1.2;
  &_size_l {
    margin-top: 30px;
    margin-bottom: 25px;
    font-size: 30px; }
  &_size_m {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 21px; } }

@include r($desktop) {
  .heading {
    &_size_l {
      font-size: 24px; }
    &_size_m {
      font-size: 20px; } } }

@include r($tablet) {
  .heading {
    &_size_l {
      margin-top: 15px;
      margin-bottom: 10px;
      font-size: 21px; }
    &_size_m {
      margin-top: 10px;
      margin-bottom: 10px;
      font-size: 19px; } } }

/* should it go from table or mobile ? */
@include r($tablet) {
  .heading {
    &_size_l {
      font-size: 19px; }
    &_size_m {
      font-size: 17px; } } }

@include r(374) {
  .heading {
    &_size_l {
      font-size: 17px; }
    &_size_m {
      font-size: 15px; } } }

.app-icons {
  display: flex;
  align-items: center;
  font-size: 0;
  margin-top: 22px;
  a {
    display: inline-block;
    margin-right: 30px;
    cursor: pointer;
    transition: opacity .25s $ease;
    &:last-child {
      margin-right: 0; }
    &:hover {
      opacity: .75; } }
  @include r($tablet) {
    max-width: 230px;
    margin-top: 10px;
    margin-bottom: -5px;
    a {
      margin-right: 0;
      &:last-child {
        margin-left: 5px; }
      .icon {
        transform-origin: left center;
        transform: scale(.9); } }
