
/* SCOPE */
.scope {
  padding: 50px 0 95px;
  background: #F3F4F5;
  &__wrapper {
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
    margin-top: 70px; }
  &__left {
    flex: 0 1 50%;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 0 15px; }

  &__right {
    flex: 0 1 50%;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 0 15px; }
  &__list {
    &-el {
      margin-bottom: 40px; }

    &-name {
      font-size: 21px;
      font-weight: 700;
      // line-height: 1.33
      margin-bottom: 15px; }
    &-desc {
      font-size: 15px;
      color: $fontSecondary; } }

  &__cta {
    margin-top: 40px; } }

@include r($desktop) {
  .scope {
    padding: 30px 0 70px;
    &__list {
      &-name {
        font-size: 20px; }
      &-desc {
        font-size: 14px; } } } }

@include r($tablet) {
  .scope {
    padding: 20px 0 35px;
    &__wrapper {
      display: block;
      margin: 20px 0 0; }
    &__left {
      flex: 0 0 auto;
      max-width: 100%;
      padding: 0 0px; }
    &__right {
      flex: 0 0 auto;
      max-width: 100%;
      padding: 0 0px; }
    &__list {
      &-el {
        margin-bottom: 20px; }

      &-name {
        font-size: 17px;
        margin-bottom: 5px; }
      &-desc {
        font-size: 13px; } }

    &__cta {
      margin-top: 20px; } } }

@include r($mobile) {
  .scope {
    &__list {
      &-name {
 }        // font-size: 13px
      &-desc {
 } } } }        // font-size: 11px

@include r(374) {
  .scope {
    &__list {
      &-name {
        font-size: 13px; }
      &-desc {
        font-size: 11px; } } } }
