* {
  box-sizing: border-box;
  outline: none; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

body, html {
  background: white;
  color: $fontColor;
  font-family: BlinkMacSystemFont, -apple-system, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', 'Roboto Rouble', sans-serif;
  font-size: 17px;
  line-height: 1.2;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; } }

.container {
  max-width: 1030px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
  &--narrow {
    max-width: 910px; }
  &-offset {
    max-width: 910px;
    margin-left: auto; }
  @include r($tablet) {
    padding-left: 35px;
    padding-right: 35px; }
  @include r(375) {
    // padding-left: 24px
 } }    // padding-right: 24px

